<!-- 主观题评分 -->
// 主观题评分
<template>
  <div class="all">
    <!-- <label style="color: #909399;
      font-weight: 500;">考试科目：</label>
       <el-select
    v-model="value"
    filterable
    remote
    reserve-keyword
    placeholder="请输入关键词"
    :remote-method="remoteMethod"
    :loading="loading"
    clearable>
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
   </el-select> -->
        <!-- <label style="color: #909399;
      font-weight: 500;">学生姓名：</label>
        <el-input  placeholder="请输入学生姓名" style="width: 200px;margin-right: 30px;" v-model="studentname" clearable>
        </el-input> -->
        <!-- <el-button type="primary" @click="search">查询</el-button> -->
        <div style="float: left;padding-top: 15px;">当前页面在线的老师：</div>
          <div class="image-container" v-for="(image, index) in teachersImage" :key="index" >
            <el-tooltip effect="dark" :content="names[index]" placement="top-start">
              <img :src="image" class="imageStyle1"  alt="image">
            </el-tooltip>
                <span class="green-dot"></span>
          </div>

      <div>&nbsp;</div>
    <el-table :data="pagination.records" border style="margin-top: 20px;">
        <el-table-column fixed="left" prop="studentId" label="学号" width="100"></el-table-column>
      <el-table-column fixed="left" prop="studentName" label="姓名" width="150"></el-table-column>
      <el-table-column prop="subject" label="考试科目" width="150"></el-table-column>
      <el-table-column prop="status" label="状态" sortable width="150">
        <template slot-scope="scope">
          <el-tag :type="scope.row.status>0 ? (scope.row.status==1? 'success': 'warning') : 'danger'">{{scope.row.status>0 ? (scope.row.status==1? '已给':'批阅中') : "未给予"}}</el-tag>
        </template>
      </el-table-column>

      <el-table-column fixed="right" label="操作" width="500">
        <template slot-scope="scope" >
          <el-button v-if="scope.row.status==0" @click="rate(scope.row.studentId,scope.row.questionId)" type="primary" size="small">评分</el-button>
          <el-button v-if="scope.row.status==1" @click="rate(scope.row.studentId,scope.row.questionId)" type="primary" size="small">重评</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pagination.current"
      :page-sizes="[6, 10]"
      :page-size="pagination.size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pagination.total"
      class="page">
    </el-pagination>
    <!-- 编辑对话框-->
    <el-dialog
      title="评分管理"
      :visible.sync="dialogVisible"
      width="60%"
      :before-close="handleClose">
      <section class="update">
        <el-form ref="form" :model="form" :inline="true"  label-width="100px" :rules="rules"  class="demo-ruleForm">
          <el-form-item label="学号:">
            <!-- <el-input v-model="form.studentId" disabled style="width: 100px;"></el-input> -->
            <el-label style="font-size: 20px;">
              {{ form.studentId }}
            </el-label>
          </el-form-item>
          <el-form-item label="姓名:">
            <!-- <el-input v-model="form.studentName" disabled style="width: 100px;"></el-input> -->
            <el-label style="font-size: 20px;">
              {{ form.studentName }}
            </el-label>
          </el-form-item>
          <el-form-item label="科目:">
            <!-- <el-input v-model="form.subject" disabled style="width: 100px;"></el-input> -->
            <el-label style="font-size: 20px;">
              {{ form.subject }}
            </el-label>
          </el-form-item>
          <el-form-item label="分值:">
            <!-- <el-input v-model="form.maxScore" disabled style="width: 50px;"></el-input> -->
            <el-label style="font-size: 20px;">
              {{ form.maxScore }}
            </el-label>
          </el-form-item>
          <el-form-item label="题目:" style="width: 800px;">
            <el-label style="width: 800px;">
              {{ form.title }}
            </el-label>
          </el-form-item>
          <el-form-item label="作答情况:">
            <editor v-model="form.answer" :min-height="160"/>
            <!-- <el-input v-model="form.answer"  type="textarea" :rows="5" style="width: 750px;font-size: 15px;"></el-input> -->
            <!-- <el-label style="width: 750px;font-size: 15px;">
              {{ form.answer }}
            </el-label> -->
          </el-form-item>
          <el-form-item label="分数:" prop="score" style="width: 1000px;">
            <el-input v-model="form.score" style="width: 60px;" maxlength="3"></el-input>
          </el-form-item>
        </el-form>
      </section>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancle(form.studentId,form.questionId,form.status)"  style="margin-right: 100px;">取 消</el-button>
        <el-button type="primary" @click="submit('form')" style="margin-right: 400px;">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Editor from '../Editor';
export default {
  components: {
    Editor
  },
  data() {
    var checkscore = (rule, value, callback) => {
        
        setTimeout(() => {
         
            if (this.form.score > this.form.maxScore) {
              callback(new Error('不能超过当前题的总分'));
            } else {
              callback();
            }
          
        }, 500);
      };
    return {
      pagination: {
        //分页后的考试信息
        current: 1, //当前页
        total: null, //记录条数
        size: 6, //每页条数
      },
      dialogVisible: false, //对话框
      form: {}, //保存点击以后当前的信息
      ptScore:'',
      teachers:null,
      teachersImage:[],
      names:[],
      options: [],
      value: [],
      list: [],
      studentname:'',
      loading: false,
      timer:null,
      rules:{
        score:[
            { validator: checkscore, trigger: 'blur'}
        ]
      },
     
    };
  },
  created() {
    this.websocketInit()
    this.websocketInitNew()
    this.timer=setInterval(() => {
      this.getSubjectiveInfo();
    }, 10000);
    // this.getSubjectiveInfo()
    // this.searchData()
    


  },
  beforeDestroy() {
    if(this.timer!=null)
    {
      console.log("aaaaaaaaaaaaaaaaaaa")
      clearInterval(this.timer)
      this.timer=null
    }
    let ws=new WebSocket(this.$websocketUpUrl+"/"+this.$jwtDecode(sessionStorage.getItem("token")).id)
    let msobj={}
      msobj.sender='客户端'
      msobj.receiver='服务器'
      msobj.message="下线"
      // console.log(ws.readyState)
      if (ws.readyState === 1) { // this.websock.readyState = 1 表示连接成功，可以立即发送信息
        ws.send(window.JSON.stringify(msobj))
      } else if (ws.readyState === 0) { // 表示正在连接，设置300ms后发送信息
        setTimeout(function () {
          ws.send(window.JSON.stringify(msobj))
        }, 300);
      } else { // 连接未创建或者创建失败，则重新创建连接，并设置500ms后发送信息
        setTimeout(function () {
          ws.send(window.JSON.stringify(msobj))
        }, 500);
      }
      let ws1=new WebSocket(this.$websocketUpUrl+"/"+this.$jwtDecode(sessionStorage.getItem("token")).id)
      let msobj1={}
      msobj1.sender='客户端'
      msobj1.receiver='服务器'
      msobj1.message="下线"
      // console.log(ws.readyState)
      if (ws1.readyState === 1) { // this.websock.readyState = 1 表示连接成功，可以立即发送信息
        ws1.send(window.JSON.stringify(msobj1))
      } else if (ws1.readyState === 0) { // 表示正在连接，设置300ms后发送信息
        setTimeout(function () {
          ws1.send(window.JSON.stringify(msobj1))
        }, 300);
      } else { // 连接未创建或者创建失败，则重新创建连接，并设置500ms后发送信息
        setTimeout(function () {
          ws.send(window.JSON.stringify(msobj1))
        }, 500);
      }

  },
  methods: {
      websocketInit(){
        let ws=new WebSocket(this.$websocketUpUrl+this.pagination.current+"/"+this.pagination.size+"/"+this.$jwtDecode(sessionStorage.getItem("token")).id)
      //发送数据
      let msobj={}
      msobj.sender='客户端'
      msobj.receiver='服务器'
      msobj.message="你好"
      // console.log(ws.readyState)
      if (ws.readyState === 1) { // this.websock.readyState = 1 表示连接成功，可以立即发送信息
        ws.send(window.JSON.stringify(msobj))
      } else if (ws.readyState === 0) { // 表示正在连接，设置300ms后发送信息
        setTimeout(function () {
          ws.send(window.JSON.stringify(msobj))
        }, 300);
      } else { // 连接未创建或者创建失败，则重新创建连接，并设置500ms后发送信息
        setTimeout(function () {
          ws.send(window.JSON.stringify(msobj))
        }, 500);
      }
        //接收数据
        
        ws.onmessage=(msg)=>{
          console.log(JSON.parse(msg.data))
        this.pagination =JSON.parse(msg.data);
      }
      
    },
    websocketInitNew(){
      let ws=new WebSocket(this.$websocketUpUrl+"/"+this.$jwtDecode(sessionStorage.getItem("token")).id)
      //发送数据
      let msobj={}
      msobj.sender='客户端'
      msobj.receiver='服务器'
      msobj.message="你好"
      // console.log(ws.readyState)
      if (ws.readyState === 1) { // this.websock.readyState = 1 表示连接成功，可以立即发送信息
        ws.send(window.JSON.stringify(msobj))
      } else if (ws.readyState === 0) { // 表示正在连接，设置300ms后发送信息
        setTimeout(function () {
          ws.send(window.JSON.stringify(msobj))
        }, 300);
      } else { // 连接未创建或者创建失败，则重新创建连接，并设置500ms后发送信息
        setTimeout(function () {
          ws.send(window.JSON.stringify(msobj))
        }, 500);
      }
        //接收数据
        ws.onmessage=(msg)=>{
          console.log(msg.data)
          var ids=String(msg.data).split(" ");
          this.$axios({
            url:'/${process.env.peopleApi}/teacher/getOnlineImage',
            method:'post',
            data:ids
          }).then(res=>{
            this.teachersImage=res.data.data.image;
            this.names=res.data.data.name;
          })
        
        
      }
    
    },
    getSubjectiveInfo() {
      //分页查询所有试卷信息
      this.$axios(`/${process.env.scoreApi}/subjective/${this.pagination.current}/${this.pagination.size}`).then(res => {
        this.pagination = res.data.data;
        console.log(this.pagination)
      }).catch(error => {});
    },
    //改变当前记录条数
    handleSizeChange(val) {
      this.pagination.size = val;
      this.getSubjectiveInfo();
    },
    //改变当前页码，重新发送请求
    handleCurrentChange(val) {
      this.pagination.current = val;
      this.getSubjectiveInfo();
    },
    rate(studentId,questionId) { //确认评分信息
      
        this.dialogVisible = true
        this.$axios(`/${process.env.scoreApi}/subjective/rate/${studentId}/${questionId}`).then(res => {
          this.form = res.data.data
          this.websocketInit()
          this.$axios('/${process.env.scoreApi}/subjective/updateStatus/'+studentId+"/"+questionId).then(r=>{
            this.getSubjectiveInfo();
            this.websocketInit()
          })
      })
    },
    cancle(studentId,questionId,oldStatus){
      
      this.$axios('/${process.env.scoreApi}/subjective/updateStatusBefore/'+studentId+"/"+questionId+"/"+oldStatus).then(r=>{
          this.getSubjectiveInfo();
          this.websocketInit()
          this.dialogVisible=false
      })

    },
    submit(formName) { //提交更改
        this.$refs[formName].validate((valid) => {
            if(valid){
                this.dialogVisible = false
                this.$axios({
                    url: '/${process.env.scoreApi}/subjective',
                    method: 'put',
                    data: {
                        studentId:this.form.studentId,
                        score:this.form.score,
                        questionId:this.form.questionId,
                        paperId:this.form.paperId
                    }
                }).then(res => {
                    console.log(res)
                    if(res.data.code ==400) {
                      this.websocketInit()
                    this.$message({
                        message: '评分成功',
                        type: 'success'
                    })
                    }
                    this.getSubjectiveInfo()
                })
            }
            else{
                console.log('error submit!!');
                return false;
            }
        })
      
    },
    handleClose(done) { //关闭提醒
      this.$confirm('确认关闭？')
        .then(_ => {
          done(); 

          this.cancle(this.form.studentId,this.form.questionId,this.form.status);
        }).catch(_ => {
        });
    },
  }
};
</script>
<style lang="less" scoped>
.green-dot {
  width: 10px; /* 设置小绿点的大小 */
  height: 10px;
  background-color: rgb(85, 255, 47); /* 设置小绿点的颜色 */
  border-radius: 50%; /* 使用border-radius属性让小绿点变成圆形 */
  position: absolute; /* 设置小绿点为绝对定位 */
  bottom: 8px; /* 调整小绿点与图片底部之间的距离 */
  right: 5px; /* 调整小绿点与图片右侧之间的距离 */
  pointer-events: none;
}
.imageStyle1{
  width: 50px;
  height: 50px;
  border-radius: 50%;
  // transition: transform 0.5s ease;
}


.image-container {
  position: relative; /* 设置容器元素为相对定位 */
  width: 50px;
  height: 60px;
  float: left;
}
.image-container :hover{
  // transform: scale(1.1);

  border: 2px solid rgb(0, 162, 255);
}
 
.all {
  padding: 0px 40px;
  .page {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .edit {
    margin-left: 20px;
  }
  .el-table tr {
    background-color: #dd5862 !important;
  }
}
.el-table .warning-row {
  background: #000 !important;
}

.el-table .success-row {
  background: #dd5862;
}
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    border-color: #409EFF;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
    border-color: #409EFF;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
    border-color: #409EFF;
  }
</style>



